@import url(https://fonts.googleapis.com/css2?family=Karla&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{background-color:#028843;width:100vw;min-height:100vh;display:flex;flex-direction:column;align-items:center;overflow:hidden}.cover-img{margin-right:-1200px;max-width:1200px;margin-top:4.75em;min-height:200px;transform:translatex(-50%)}@media (min-width: 768px){.cover-img{margin-top:5.5em}}.image-with-text-overlay{background-image:url(/static/media/trucks.6557fafa.png);width:100vw;max-width:1200px;display:flex;justify-content:center;align-items:center;height:300px;margin-bottom:60px;color:white;margin-top:4em}a{text-decoration:none;color:#ffbc42;font-weight:600;transition:all 300ms ease-in-out;border-bottom:0px solid rgba(255,255,255,0)}a:hover{border-bottom:5px solid #ffbc42;color:rgba(255,255,255,0.8)}html{scroll-behavior:smooth}body{line-height:1.4;font-family:"Karla", sans-serif}

.nav{display:flex;flex-direction:row;justify-content:center;align-items:center;padding-top:20px;height:3em;width:100vw;background-color:#013c1e;position:fixed;padding-bottom:10px}@media (min-width: 768px){.nav{padding-bottom:20px}}.nav-logo{width:5.5em;position:relative;left:2em;display:none}@media (min-width: 768px){.nav-logo{padding-bottom:20px;position:fixed;left:1em;top:0em;display:flex}}.nav-link{margin:15px 5px;text-decoration:none;font-size:20px;line-height:0;color:#fff}@media (min-width: 768px){.nav-link{margin:10px 30px;line-height:1}}.nav-socials{position:relative;right:2em;display:none;opacity:0.7}@media (min-width: 768px){.nav-socials{display:flex;position:fixed;right:1em;top:0em;display:flex}}

.img-block{display:flex;flex-direction:column;align-items:center}.img-block h2{margin-left:0.5em}.img-block-content{display:flex;flex-direction:row;justify-content:space-around;margin-top:20px;max-width:1000px}@media (min-width: 768px){.img-block-content{width:80%}}.img-block-content img{width:50px;margin-bottom:20px;margin-left:20px;margin-right:20px}.img-block-content-small img{margin-left:5px;margin-right:5px}.img-block-content-large{flex-wrap:wrap;justify-content:center;align-items:center}.img-block-content-large img{width:150px;margin-bottom:40px}.img-block a:hover{border-bottom:none;opacity:0.7}

.content-img-block{display:flex;flex-direction:column;justify-content:center;margin-left:1em;margin-right:1em}@media (min-width: 768px){.content-img-block{max-width:70vw}}.content-img-block-title{color:white;font-size:2.2em;line-height:1.1}.content-img-block-title h2{margin-bottom:0.5em}.content-img-block-content{display:flex;flex-direction:column;align-items:center}@media (min-width: 768px){.content-img-block-content{flex-direction:row;align-items:flex-start}}.content-img-block-content-format{display:flex;flex-direction:column}.content-img-block-content-p{color:white}@media (min-width: 768px){.content-img-block-content-p{max-width:40vw}}.content-img-block-img{width:250px;height:250px;border-radius:200px}@media (min-width: 768px){.content-img-block-img{margin-left:4em;width:300px;height:300px;margin-top:2em}}

.about-section{padding-top:4em}

.collections-section{padding-top:4em}

.sales-section{padding-top:4em}

.content-block{display:flex;flex-direction:column;align-items:center;text-align:center;color:#ffffff;margin-left:0.5em;margin-right:0.5em;justify-content:center}.content-block-title{font-size:2em}.content-block-title h2{margin-bottom:0em}.content-block-subtitle{font-size:1.5em}@media (min-width: 768px){.content-block-content{align-items:flex-start;max-width:40vw}}

.contact-section{display:flex;flex-direction:column;align-items:center;padding-top:4em;padding-bottom:4em}

.partners-section{background-color:#ffffff;width:100vw;padding-bottom:4em;padding-top:4em}

